import './App.css';
import React from "react";
import EntryBar from './EntryBar/EntryBar';
import TaskList from './TaskList/TaskList';
import CompletedList from './CompletedList/CompletedList';

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      tasks: [],
      completed: []
    }
    this.addTask = this.addTask.bind(this);
    this.moveTask = this.moveTask.bind(this);
    this.removeTask = this.removeTask.bind(this);
  }

  addTask(task) {
    let tasks = this.state.tasks;
    if (tasks.includes(task)) {
      return;
    }
    tasks.push(task);
    this.setState({tasks: tasks});
  }

  moveTask(task) {
    let newTasks = this.state.tasks.filter(el => el !== task);
    let newCompleted = this.state.completed;
    newCompleted.push(task);
    this.setState({tasks: newTasks, completed: newCompleted});
  }

  removeTask(task) {
    let newCompleted = this.state.completed.filter(el => el !== task);
    this.setState({completed: newCompleted});
  }

  render() {
    return (
      <div className='app'>
        <h1>TO DO LIST</h1>
        <EntryBar onAdd={this.addTask}/>
        <div className='container'>
          <TaskList tasks={this.state.tasks} onCompleted={this.moveTask}/>
          <CompletedList completed={this.state.completed} onRemove={this.removeTask}/>
        </div>
       </div>
    );
  }
}

export default App;
