import './Task.css'
import React from 'react';

class Task extends React.Component {

    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick() {
        if (this.props.token === 'V') {
            this.props.onCompleted(this.props.task);
        }
        else {
            this.props.onRemove(this.props.task);
        }
     }

    render() {
        return (
            <div className='task'>
                <p>{this.props.task}</p>
                <button className={this.props.token} onClick={this.handleClick}>{this.props.token}</button>
            </div>
        );
    }
}

export default Task;